*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

button,
input,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
}

/* Body Styles */
body {
  font-family: Helvetica, Arial, system-ui;
  height: 100vh;
  line-height: 1.5;
}

#container {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  grid-template-rows: auto 1fr;
  height: 100vh;
}

#header {
  min-height: 10vh;
  grid-column: 1/-1;
  grid-row: 1/2;
}

#logo {
  position: absolute;
  top: 2%;
  left: 2%;
  width: 100px;
  height: auto;
}

#left-sidebar {
  margin: 10px;
}

#projects {
  min-height: 80vh;
  padding: 10px;
}

.project-button {
  display: block;
  border: none;
  cursor: pointer;
  width: 80%;
  margin-bottom: 10px;
  
}

.add-project-button {
  width: 80%;
}

#tasks {
  min-height: 80vh;
  padding: 10px;
}

#right-sidebar {
  margin: 10px;
}

#due-dates {
  min-height: 80vh;
  padding: 10px;
}
